import { Box, Card, CardActionArea, CardMedia, Grid, Stack, Typography } from "@mui/material"

import Slider from 'react-slick'

const imgsban = [
    '../images/introduce/design/1.JPG',
    '../images/introduce/design/2.JPG'
]
const ActionAreaCard2 = ({ src }: { src: string }) => {
    return (
        <Card>
            <CardActionArea>
                <CardMedia
                    component="img"
                    image={src}
                    alt="green iguana"
                />
            </CardActionArea>
        </Card>
    )
}
const settings = {
    dots: true,
    infinite: true,
    autoplay: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1
}
const SelectionDesign = () => {
    return <Box>
        <Box sx={{ background: '#4ca2ff' }}>
            <Stack direction='row' alignItems='center' spacing={1}>
                <img src="../images/Icon1/IMG_7006.png" style={{ width: '100px' }} alt='airplan' />
                <Box lineHeight={1}>
                    <Typography sx={{ margin: '0' }} variant="h6" color='whitesmoke'>Designer</Typography>
                    <Typography variant="caption" color='whitesmoke'>ผู้รับจ้างออกแบบ</Typography>
                </Box>
            </Stack>
        </Box>
        <Grid container={true} spacing={1}>
            <Grid item={true} xs={12} md={6}>
                <Box marginTop={4}>
                    <Slider {...settings}>
                        {imgsban.map((banner) => (
                            <Box key={banner} padding="8px 4px" boxSizing="border-box">
                                <ActionAreaCard2 src={banner} />
                            </Box>
                        ))}
                    </Slider>
                </Box>
            </Grid>
            <Grid item={true} xs={12} md={6}>
                <Box marginTop={4}>
                    <Typography variant="h4" color='#007AFF'>
                        ระบบการคัดเลือกผู้รับจ้างออกแบบงานก่อสร้างหรือติดตั้ง
                    </Typography>
                    <Typography style={{ lineHeight: '2rem' }}>
                        เป็นรูปแบบการจัดหางานจ้างออกแบบงานก่อสร้างหรือติดตั้ง
                        มี 2 วิธีคือ <br />
                        <strong style={{ marginLeft: '20px' }}>
                            1) วิธีเซลฟ์ แพลน (Self Plan)
                        </strong>
                        เป็นงานจ้างที่ผู้ว่าจ้างจัดหาผู้ทำแบบก่อสร้าง/ติดตั้งเอง หรือมีแบบแบบก่อสร้าง/ติดตั้งอยู่แล้วหรือจัดหาแบบโดย ระบบแอร์ แพลน (Air Plan)  โดยเเบบที่ผู้ว่าจ้างนำมาต้องมีรายละเอียดครบทั้งงานสถาปัตย์ งานโครงสร้าง งานระบบ   หรือตามความจำเป็นที่ผู้รับจ้างก่อสร้างหรือติดตั้งควรทราบเพื่อให้ประเมินการยื่นราคาและข้อเสนอทางเทคนิคในการเข้าการคัดเลือกถูกต้องครบถ้วน<br />
                        <strong style={{ marginLeft: '20px' }}>
                            2) วิธีชูส แพลน (Choose Plan) เป็นงานจ้างออกแบบที่ผู้ว่าจ้างเลือกจ้างผู้รับจ้างออกแบบจากการยื่นข้อเสนอในระบบการคัดเลือก แบ่งเป็น 2 แบบ ได้แก่<br />
                        </strong>
                        <span>
                            <span style={{ marginRight: '20px' }} />  2.1) ทั่วไป คือ การที่ผู้ว่าจ้างต้องการหาผู้รับจ้างออกเเบบที่มีคุณสมบัติตรงกับความต้องการของผู้ว่าจ้าง โดยประกาศเชิญชวนผ่านระบบสารสนเทศให้เข้ารับการคัดเลือก<br />
                        </span>
                        <span style={{ marginRight: '20px' }} />2.2) เทียบเชิญ คือ งานจ้างก่อสร้าง/ติดตั้งที่มีความเฉพาะเจาะจงหรือความจำเพาะ     ที่ต้องมีผู้เชี่ยวชาญในด้านต่างๆ ซึ่งผู้ว่าจ้างเสนอให้ไปเทียบเชิญเป็นผู้รับจ้างที่อยู่ในระบบสารสนเทศที่มีความเชี่ยวชาญเฉพาะเจาะจงในด้านนั้นที่ผู้ว่าจ้างต้องการมาเข้าร่วมการคัดเลือก

                        ในกรณีผู้ว่าจ้างไม่มีแบบแปลนในการก่อสร้าง ให้ผู้ว่าจ้างกรอกรายละเอียดการเข้าร่วมการคัดเลือกด้วยวิธีself plan และผู้ว่าจ้างต้องกรอกข้อมูล ในระบบที่บริษัทได้กำหนดไว้โดยให้ผู้ว่าจ้างระบุรายละเอียดของสิ่งก่อสร้างที่ผู้ว่าจ้างต้องการจะสร้าง เพื่อเป็นข้อมูลเบื้องต้นสำหรับผู้รับจ้างออกเเบบ ทั้งนี้ หากผู้ว่าจ้างต้องการที่จะใช้วัสดุอุปกรณ์ชนิดใดหรือ อื่นๆ สามารถระบุรายละเอียดเพิ่มเติมได้

                        หากผู้ว่าจ้างได้มีแบบแปลนสิ่งก่อสร้างอยู่แล้วให้ผู้ว่าจ้างส่งรูปแบบแปลนตามระบบที่บริษัทกำหนด ให้บริษัท และบริษัทจะเพื่อดำเนินการคัดเลือกหาผู้รับจ้างก่อสร้าง/ติดตั้งผู้รับจ้างควบคุมและตรวจสอบงานให้ผู้ว่าจ้าง

                        วิธีselfplan ให้ผู้ว่าจ้างกรอกรายละเอียด และความต้องการ ผ่านระบบพร้อมแนบเอกตามที่กำหนดในระบบ หลังจากได้รับรายละเอียดบริษัทจะประกาศเพื่อหาผู้รับจ้างออกแบบ โดยผู้รับจ้างออกแบบจะยื่นข้อเสนอ เช่น ราคา รายละเอียดอื่นๆ ให้ผู้ว่าจ้างได้พิจารณาเลือกผู้ยื่นข้อเสนอให้มาดำเนินงานรับจ้างออกแบบ
                        <br />
                        <span style={{ marginRight: '20px' }} />วิธีchooseplanควรพิจารณาเสนอแนะทั้งนี้ให้กระทำได้ในกรณีดังต่อไปนี้<br />
                        (1) เกณณ์ประเมินคะเเนนที่บริษัทจัดทำในระบบสารสนเทศของบริษัท โดยผู้ว่าจ้างจะให้คะเเนนตามเกณฑ์ประเมินคะเเนนกับผู้ยื่นข้อเสนองานจ้างออกแบบที่เข้ารับการคัดเลือกทุกราย<br />
                        (2) ใช้กับงานจ้างที่ผู้ว่าจ้าง และผู้ยื่นข้อเสนองานจ้างออกแบบตกลงกัน<br />
                        (3)งานจ้างออกแบบก่อสร้าง ต้องปฎิบัติตามมาตรฐานตามหลักวิชาชีพ และมีบุคคลากรซึ่งสามารถทํางานนั้นได้เป็นการทั่วไป เว้นแต่ <br />บริษัทนั้นมีมาตรฐานตามหลักวิชาชีพซึ่งสามารถทํางานนั้นได้เป็นการทั่วไป
                        (4) กรณีอื่นตามที่บริษัทกำหนด
                        <br />
                        <span style={{ marginRight: '20px' }} />วิธีchooseplanจะทำการโดยบริษัทประกาศผ่านระบบสารสนเทศบริษัท ให้ผู้ยื่นข้อเสนอที่มีคุณสมบัติตรงตามเงื่อนไขที่ผู้ว่าจ้างกำหนดหรือที่ผู้ว่าจ้างเทียบเชิญมาเสนอราคาในการคัดเลือก ถ้าการคัดเลือกต้องมีผู้ยื่นข้อเสนอไม่น้อยกว่าสามรายให้เข้ายื่นข้อเสนอ หากผู้ว่าจ้างไม่ประสงค์จะเลือกผู้ยื่นข้อเสนอ ทางบริษัทจะคืนเงินประกันการคัดเลือก 50%  เว้นแต่ ในการเข้าคัดเลือกในครั้งนั้นมีบริษัทที่ได้เข้าร่วมน้อยกว่าสามบริษัทและผู้ว่าจ้างเห็นสมควรที่จะเลือกบริษัทใดบริษัทหนึ่ง
                        <br />
                        <span style={{ marginRight: '20px' }} />วิธีเข้าเป็นงานจ้างออกแบบผ่านระบบของบริษัทกระทำได้ในกรณีดังต่อไปนี้<br />
                        (1) ผู้ว่าจ้างมีความประสงค์เข้าการคัดเลือก<br />
                        (2) เป็นงานจ้างที่มีลักษณะซับซ้อน หรือซับซ้อนมาก<br />
                        (3) เป็นงานเกี่ยวกับการออกแบบ <br />หรือใช้ความคิดเช่นรูปแบบสิ่งก่อสร้างซึ่งผู้ว่าจ้างต้องการราย  ละเอียดเพิ่มเติมที่จะใช้กำหนดรายละเอียดเบื้องต้นได้<br />
                        (4) เป็นงานที่จําเป็นต้องให้ผู้ให้บริการรายเดิมทำต่อจากงานที่ได้ทำไว้แล้ว<br />
                        (5) กรณีอื่นตามที่บริษัทกำหนด<br />
                    </Typography>
                </Box>

            </Grid>
        </Grid>
    </Box>
}

export default SelectionDesign